.locate-container {
    height: 100vh;
    padding-top: 6rem;
    width: 100%;
}

.locate-container h1 {
    text-align: center;
    color: var(--orange);
}

.locate-section{
    width: fit-content;
    margin: 40px auto;
}
.locate-section h3 {
    text-align: center;
    color: var(--orange);
    margin: 10px 0;
}
.locate-section ul li {
    color: var(--blue);
    list-style-type: none;
    margin: 5px 0;
}
.locate-section ul li:hover {
    font-weight: bolder;
}

