@media (min-width:300px){
    .locate-container  {
        font-size: 1.2em;
    }
}

@media (min-width:600px) {
    .locate-container h1 {
        font-size: 3em;
    }
    .locate-section {
        font-size: 1.5em;
    }
}
@media (min-width:700px) {
    .locate-container h1 {
        font-size: 3em;
    }
    .locate-container {
        padding-top: 6rem;
    }
   
}
@media (min-width:900px) {
    .locate-container h1 {
        font-size: 3.5em;
    }
    .locate-container {
        padding-top: 6rem;
    }
    .locate-section {
        font-size: 1.8em;
    }

    .locate-section:last-child{
        padding-bottom: 6rem;
    }

    .locatepaintmark {
        transform: scale(2) translateX(1rem);
    }
   
}

@media (min-width:1100px) {
    .locate-container h1 {
        font-size: 3em;
    }
    .locate-container {
        padding-top: 12rem;
    }
   
    .locatepaintmark {
        transform: scale(3) translate(2rem, 1rem);
    }
   
}

@media (min-width:1500px) {
    
   .locate-container h1 {
       font-size: 5em;
   }
    .locate-section {
        font-size: 2.5em;
    }


    .locatepaintmark {
        transform: scale(4) translate(2rem, 1rem);
    }
   
}