.contact-container {
    /* position: absolute; */
    /* height: 100vh; */
    padding-top: 6rem;
    width: 100%;
   overflow-x: hidden;
}

.contact-container h1 {
   margin-top: 10px;
   text-align: center;
   color: var(--orange);
}
.splash {
    position: absolute;
}

.contact-search {
    margin: 20px 0;
    display: flex;
    justify-content: center;
  
}

.contact-search input {
    position: relative;
    border: 3px solid var(--orange);
    border-radius: 10px;
    padding: 5px;
    font-size: 0.8em;
    margin: 0 10px;
    outline: none;
    color: var(--blue);
    z-index: 10;
}

.contact-search button {
    color: white;
    background-color: var(--blue);
    padding: 0 20px;
    border-radius: 10px;
    outline: none;
    border: none;
}

.numbers, .emails {
    width: fit-content;
    margin: 40px auto;
    color: var(--blue);
}

.numbers h3, .emails h3 {
    color: var(--orange);
    text-align: center;
    margin: 10px 0;
}

.numbers ul li, .emails ul li {
    list-style-type: none;
    margin: 5px 0;
}

.paintbrush {
  position: absolute;
  right: -30px;
  top: 300px;
  opacity: 0.1;
}
.numbers {
    padding-bottom: 7rem;
}

.paintbrush {
    position: absolute;
    left: 50%;
    transform: translateX(-10%);
    opacity: 0.1;
}


@media (min-width:600px){
    .contact-container h1 {
        font-size: 3em;
    }

    .contact-search {
        font-size: 2em;
    }
    .contact-search button {
        font-size: 1em;
    }

    .emails, .numbers {
        font-size: 1.5em;
    }
    .paintbrush {
        transform: translate(50%, 100px);
    }
}

@media (min-width:700px){

    .contact-container {
        width: 100vw;
        height: 100vh;
        overflow-x: hidden;
    }
    .paintbrush {
        /* bottom: 0px; */
        transform: rotateZ(15deg) translate(200px, 50px) scale(1.5);
    }

    .splash {
        transform: scale(2);
    }
   }

@media (min-width:1100px){
    .contact-container h1 {
        padding-top: 8rem;
        font-size: 4em;
    }

   .paintbrush{
       transform: translate(80%, 200px) scale(2);
   }
   
}

@media (min-width:1300px){

    .emails, .numbers {
        font-size: 2em;
    }

    .contact-search{
        font-size: 3rem;
    }

    .paintbrush{
        transform: translate(90%, 200px) scale(2);
    }
}
@media (min-width:1300px){

    .contact-container {
        padding-top: 3rem;
    }

    .contact-container h1{
        font-size: 5em;
    }
    .emails, .numbers {
        font-size: 2em;
    }

    .contact-search{
        font-size: 3rem;
    }

    .paintbrush {
        transform: scale(3) translate(50%, 100px) ;
    }

    .splash{
        transform: scale(3);
    }
}

@media (min-width:1700px){
    .splash{
        transform: translateX(50%) scale(3);
    }
}
@media (min-width:2000px){
    .contact-container{
        overflow: auto;
    }
    .splash{
        transform: translateX(60%) scale(4);
    }

    /* .numbers {
        padding-bottom: 6rem;
    } */
}

