.about-container {
    height: 100vh;
    overflow-y: auto;
    overflow-x: hidden;
    padding-bottom: 10rem;
    width: 90%;
    margin: 0px auto;
}

.about-us {
    padding-top: 80px;
    color: var(--blue);
    /* width: 90%; */
    
}

section {
    margin: 20px 0;
}

section h1 {
    font-size: 2em;
    color: var(--orange);
    text-align: center;
    margin-bottom: 20px;
}

.about-us .rotate {
    background-color: rgb(216, 216, 216);
    background-image: url("../../designassets/droplet.png");
    background-repeat: no-repeat;
    background-position: 70% 70%;
    padding: 20px;
    box-shadow: 1px 1px 4px 0px rgb(66, 66, 66);
    transform: rotateZ(3deg);
}

.about-us .rotate div {
    transform: rotateZ(-3deg);
}



.curriculum div{
    color: var(--blue);
    background-color: rgb(216, 216, 216);
    background-image: url("../../designassets/fullmark.png");
    background-repeat: no-repeat;
    padding: 20px;
    box-shadow: 1px 1px 4px 0px rgb(66, 66, 66);
    background-position: 50% 50%;
}

.faculty div{
    color: var(--blue);
    background-color: rgb(216, 216, 216);
    padding: 20px;
    box-shadow: 1px 1px 4px rgb(66, 66, 66);
}

#about-icon-container{
    display: flex;
    justify-content: space-evenly;
    font-size: 2.5em;
}

section h4 {
    color: var(--blue);
    text-align: center;
    font-size: 1.5em;
    margin-bottom: 20px;
}

@media (min-width:600px){
    .rotate {
        width: 75%;
        margin: 40px auto;
    }

    .curriculum {
        width: 75%;
        margin: 80px auto;
    }

    .faculty {
        width: 75%;
        margin: 80px auto;
    }

    .about-container {
        font-size: 1.5em;
    }
   
}

@media (min-width:1100px){
    .rotate {
        width: 60%;
        margin: 0 auto;
    }

    .curriculum {
        width: 60%;
        margin: 40px auto;
    }

    .faculty {
        width: 60%;
        margin: 40px auto;
    }

    .about-container {
        font-size: 2em;
        padding-top: 6rem;
    }
}

@media (min-width:1800px){
    .rotate {
        width: 50%;
        margin: 0 auto;
    }

    .curriculum {
        width: 50%;
        margin: 40px auto;
    }

    .faculty {
        width: 50%;
        margin: 40px auto;
    }

    .about-container {
        font-size: 2em;
        padding-top: 6rem;
    }
    .about-container .rotate div{
       
        font-size: 1.2em;
        font-weight: bold;
        padding: 40px;
    }

    .faculty div{
        font-size: 1.2em;
        font-weight: bold;
        padding: 40px;
    }
    .curriculum div{
        font-size: 1.2em;
        font-weight: bold;
        padding: 40px;
    }
   
}

