.funding-container{
    height: 100vh;
    padding-top: 6rem;
    width: 100%;
    font-size: 1.5em;
}

.funding-container h1{
    text-align: center;
    color: var(--orange);
}


#finding-splash{
    position: absolute;
    top: 140px;
    width: 200px;
    height: 350px;
    z-index: -1;
  }

  .funding-section {
      color: var(--blue);
      padding: 10px;
      margin: 0 auto;
      width: 80%;
      position: relative;
      bottom: 100px;
      font-size: 1.2em;
      background: rgba(200, 200, 200, 0.7);
  }

  .spacer {
      padding-bottom: 1rem;
  }

  strong {
      font-weight: bolder;
      background-color: var(--orange);
      padding: 2px;
      border-radius: 5px;
  }

  .paintmark{
     display: none;
  }

  @media (min-width:600px){
      .funding-container {
          font-size: 2em;
      }

      #funding-splash svg{
          transform: scale(2) translate(2rem, 1rem);
      }
  }

  @media (min-width:900px){
      .funding-container {
          font-size: 2.5em;
      }

      #funding-splash svg{
          transform: scale(2.5) translate(2rem, 1rem);
      }
  }

  @media (min-width:900px){
      .funding-container {
          padding-top: 8rem;
      }
  }

  @media (min-width:1100px){
      .paintmark {
          display: inline-block;
          position: absolute;
          top: 20rem;
          right: 10px;
          opacity: 0.7;
          z-index: -1;
      }
  }

  @media (min-width:1400px){
      .funding-container {
          font-size: 3em;
      }
      #funding-splash svg{
        transform: scale(4) translate(2.6rem, 2rem);
    }
  }

  @media (min-width:1700px){
      .funding-container {
          font-size: 3em;
          overflow: hidden;
      }
      #funding-splash svg{
        transform: scale(4) translate(2.6rem, 2rem);
        }
  }