
main {
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow-y: scroll;
    overflow-x: hidden;
    width: 100%;
  }
  
 main h1{
    font-size: 2.5em;
    padding-top: 100px;
    text-align: center;
    color: var(--orange);
  
  }

  h1 span {
    color: var(--blue);
  }
  #home-splash{
    position: absolute;
    top: 150px;
    /* opacity: 0.3; */
    width: 200px;
    height: 350px;
    z-index: -1;
    background-image: url("../../designassets/fullmark.png");
    background-repeat: no-repeat;
  }
  
  #home-text-container{
    padding: 10px;
    width: 80%;
    font-size: 1.4em;
    margin-top: 80px;
    margin-left: 20px;
    box-shadow: 1px 1px 4px rgb(0, 0, 0);
    transform: rotateZ(-10deg);
    color: var(--blue);
  }
  
  #home-text-container h2 {
    font-weight: bolder;
  }
  #home-text-container h4 {
    font-weight: lighter;
  }
  
  #apply-button-container{
    width: fit-content;
    margin: 0 auto;
  }
  
  #home-apply-button {
    cursor: pointer;
    outline: none;
    padding: 15px;
    border-radius: 10px;
    font-size: 1.7em;
    color: var(--blue);
    border: 5px solid var(--orange);
    font-weight: bolder;
    background-color: transparent;
    margin-top: 60px;
    font-family: 'Montserrat', sans-serif;
    font-weight: bolder;
  }
  
  #home-paintbrush{
    transform: rotateZ(15deg) translateX(-50%);
    position: absolute;
    bottom: 3rem;
    z-index: -1;
    opacity: 0.1;
    left: 50%;
  }

 


  @media (min-width:500px){
    #home-splash{
      transform: scale(2) translateY(4rem);
    }
    #home-text-container{
      
      font-size: 2em;
    }

    #home-apply-button {
      transform: scale(1.5);
    }
  }
  @media (min-width:700px){

    .bigname {
      transform: scale(2);
    }
    #home-splash{
      transform: scale(2.5) translateY(4rem);
    }
    #home-text-container{
      font-size: 2.5em;
      margin-top: 120px;
    }

    #home-apply-button {
      transform: scale(1.5);
    }

    #home-paintbrush{
      transform: scale(1.5)
    }
  }
  @media (min-width:800px){

    #home-text-container{
      font-size: 3em;
      margin-top: 140px;
    }
  }
  @media (min-width:1100px){

    #home-text-container{
      margin: 0 auto;
      width: 40rem;
      font-size: 3.5em;
      margin-top: 200px;
    }

    #home-paintbrush {
      bottom:1rem;
      right: 0;
      transform: translateX(70%) scale(2);
    }

    .bigname {
      padding-top: 8rem;
    }
  }
  @media (min-width:1500px){
    main{
      overflow-y: auto;
      overflow: hidden;
    }
    #home-paintbrush {
      transform: scale(3) rotateZ(15deg) translateX(30%);
      bottom: -50px;
      right: 0px;
    }
   
  }
