.reviews-container {
    height: 100vh;
    padding-top: 6rem;
    width: 100%;
}

.reviews-container h1{
    color: var(--orange);
    text-align: center;
}

.reviews-container figure {
    font-size: 1.1em;
    padding: 20px;
    margin: 20px auto;
    width: 80%;
    background-color: rgb(216, 216, 216);
    border-radius: 20px;
    box-shadow: 1px 1px 4px rgb(126, 126, 126);
}

.reviews-container figcaption {
    color: var(--orange);
    text-align: right;
}

.reviews-container blockquote {
    color: var(--blue);
    margin: 10px 0;
    font-weight: bold;
}

.reviews-container span {
    color: var(--orange);
}

.review-wrapper {
    padding-bottom: 8rem;
}

@media (min-width: 600px) {
    .reviews-container {
        font-size: 1.2em;
    }
}

@media (min-width: 1100px) {
    .reviews-container {
        font-size: 1.8em;
        padding-top: 8rem;
    }
    .review-wrapper{
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}
