header {
  top: 0;
    position: fixed; 
    width: 100vw;
    height: 100px;
    background: url("../../designassets/header.png") no-repeat;
    background-size: 100% 100px;
    z-index: 10;
 
  }
  
  .upper-header-container {
   padding: 10px;
    width: calc(100vw - 20px);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  header .logo-container {
    position: relative;
  }
  
  .header-navlink{
    color: white;
    list-style-type: none;
    text-decoration: none;
    font-size: 2em;
    display: none;
  }

  .menu-icon {
    cursor: pointer;
    font-size: 2em;
    color: var(--orange);
    padding-right: 20px;
  }

 
  
  .name {
    text-align: center;
    font-size: 2.5em;
    padding-top: 10px;
    display: none;
  }
  
  .name span:nth-child(1){
    color: var(--orange);
  }
  
  .name span:nth-child(2){
    color: white;
  }
  
   

  .open {
    z-index: 2;
    top: 0;
    position: absolute;
    right: 0;
    transition:  500ms ease-in-out;
    width: 60%;
      height: 100vh;
      background-color: var(--blue);
      color: white;
      overflow-y: auto;
  }
  .closed {
    z-index: 3;
    position: absolute;
    right: -100%;
    top: 0;
    transition:  500ms ease-in-out;
    width: 60%;
      height: 100vh;
      background-color: var(--blue);
      color: white;
   
  }



  

  nav ul{
    margin: 0;
    padding: 0;
    padding-bottom: 6rem;
  }

  nav ul li {
    list-style-type: none;
    font-size: 1.8em;
    padding: 20px;
    margin: 0 10px;
    border-bottom: 2px solid var(--orange);
  }
  nav ul li:active {
    font-weight: bolder;
    background-color: var(--orange);
  }

  .closenav {
    color: var(--orange);
    font-size: 3em;
    margin-left: 75%;
    margin-top: 10px;
  }

  @media (min-width:600px){
    .open {
      width: 40%;
    }
  }
  @media (min-width:900px){
    .open {
      width: 30%;
    }
  }
  @media (min-width:1100px){

    .header-navlink{
      color: white;
      list-style-type: none;
      text-decoration: none;
      font-size: 2em;
      display: block;
    }
    
    .menu-icon-container {
      width: 60%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    
    .menu-icon {
      width: 2em;
      height: 2em;
    }

    .logo-container svg {
      transform: scale(2) translate(1.4rem, 0.5rem);
    }

    header {
      background-size: 100% 150px;
      height: 150px;
    }
  }

  @media (min-width:1300px){

    .open {
      width: 20%;
    }

    .header-navlink{
      color: white;
      list-style-type: none;
      text-decoration: none;
      font-size: 2.5em;
      display: block;
    }
    
    .menu-icon-container {
      width: 50%;
      display: flex;
      justify-content: space-between;
    }
    

    header {
      background-size: 100% 150px;
    }
  }

  @media (min-width:2000px){

    .header-navlink{
      color: white;
      list-style-type: none;
      text-decoration: none;
      font-size: 2.5em;
      display: block;
    }
    
    .menu-icon-container {
      width: 40%;
      display: flex;
      justify-content: space-between;
    }
    

    header {
      background-size: 100% 150px;
    }
  }

  
