.apply-container {
    height: 100vh;
    padding-top: 6rem;
    width: 100%;
}

.apply-container input {
    outline: none;
}
.apply-container form {
    width: 80%;
    margin: 40px auto;
    padding-bottom: 5rem;
}

.apply-container form label {
    display: block;
   
}



.apply-container h1 {
    text-align: center;
    color: var(--orange);
}
.normal {
    margin: 20px auto;
}

.normal input {
    border: 2px solid var(--orange);
    width: 100%;
    font-size: 1.2em;
    color: var(--blue);
    padding: 5px;
    border-radius: 20px;
}

.highest {
    /* width: 100%; */
    margin: 20px auto;
    color: var(--blue);
}

.highest select {
    width: 100%;
    outline: none;
    border: 2px solid var(--orange);
    padding: 5px;
    font-size: 1.2em;
    border-radius: 20px;
    color: var(--blue);
}

.apply-container label {
    color: var(--blue);
    margin: 10px;
}



.school-list {
    width: 100%;
    margin: 20px auto;
}

.school-list input {
    width: 100%;
    font-size: 1.2em;
    color: var(--blue);
    padding: 5px;
}


.school-list input:nth-child(1){
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    border-bottom: none;
}

.school-list input {
    border: 2px solid var(--orange);
}


.school-list input:nth-child(3){
    border-top: none;
}
.school-list input:nth-child(4){
    border-top: none;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}

.files {
    width: 100%;
    margin: 20px auto;
}

.files input {
    border: 2px solid var(--orange);
    border-radius: 20px;
    font-size: 1.2em;
}


.files div button{
    outline: none;
    display: inline;
    width: 40%;
    font-size: 1.2em;
    color: white;
    background-color: var(--blue);
    border: none;
    border-radius: 20px;
    padding: 5px;
}

.files div span {
    color: var(--blue);
    font-size: 1.2em;
    overflow: hidden;
}




.apply-button {
    display: block;
    font-size: 1.5em;
    width: 80%;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 3rem;
    color: white;
    background-color: var(--blue);
    border: none;
    padding: 10px;
    border-radius: 20px;
}


@media (min-width:500px){
    .apply-container{
        font-size: 1.5em;
    }
    

}

@media (min-width:700px){
    .apply-container{
        padding-top: 7rem;
    }

    .apply-container input {
        padding: 10px;
    }
    .files div button {
        width: 30%;
    }
}

@media (min-width:800px){
    .apply-container form {
        width: 60%;
    }

    .files div button {
        width: 30%;
    }
}

@media (min-width:1100px){
    .apply-container {
        padding-top: 12rem;
    }
}

@media (min-width:1400px){
    .apply-container{
        font-size: 2em;
    }
}

@media (min-width:1400px){
    .apply-container form{
        width: 45%;
    }
}

@media (min-width:2000px){
    .apply-container form{
        width: 35%;
    }
}