@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

:root{
  --blue: #102FA3;
  --orange: #F07C01;
}

#root{
  height: 100vh;
}

body {
  overflow-x: hidden;
  font-family: 'Montserrat', sans-serif;
  width: 100vw;
  /* height: 100vh; */
}


main {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100vh;
    overflow-y: scroll;
    overflow-x: hidden;
    width: 100%;
  }
  
 main h1{
    font-size: 2.5em;
    padding-top: 100px;
    text-align: center;
    color: var(--orange);
  
  }

  h1 span {
    color: var(--blue);
  }
  #home-splash{
    position: absolute;
    top: 150px;
    /* opacity: 0.3; */
    width: 200px;
    height: 350px;
    z-index: -1;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
  }
  
  #home-text-container{
    padding: 10px;
    width: 80%;
    font-size: 1.4em;
    margin-top: 80px;
    margin-left: 20px;
    box-shadow: 1px 1px 4px rgb(0, 0, 0);
    -webkit-transform: rotateZ(-10deg);
            transform: rotateZ(-10deg);
    color: var(--blue);
  }
  
  #home-text-container h2 {
    font-weight: bolder;
  }
  #home-text-container h4 {
    font-weight: lighter;
  }
  
  #apply-button-container{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
  }
  
  #home-apply-button {
    cursor: pointer;
    outline: none;
    padding: 15px;
    border-radius: 10px;
    font-size: 1.7em;
    color: var(--blue);
    border: 5px solid var(--orange);
    font-weight: bolder;
    background-color: transparent;
    margin-top: 60px;
    font-family: 'Montserrat', sans-serif;
    font-weight: bolder;
  }
  
  #home-paintbrush{
    -webkit-transform: rotateZ(15deg) translateX(-50%);
            transform: rotateZ(15deg) translateX(-50%);
    position: absolute;
    bottom: 3rem;
    z-index: -1;
    opacity: 0.1;
    left: 50%;
  }

 


  @media (min-width:500px){
    #home-splash{
      -webkit-transform: scale(2) translateY(4rem);
              transform: scale(2) translateY(4rem);
    }
    #home-text-container{
      
      font-size: 2em;
    }

    #home-apply-button {
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
    }
  }
  @media (min-width:700px){

    .bigname {
      -webkit-transform: scale(2);
              transform: scale(2);
    }
    #home-splash{
      -webkit-transform: scale(2.5) translateY(4rem);
              transform: scale(2.5) translateY(4rem);
    }
    #home-text-container{
      font-size: 2.5em;
      margin-top: 120px;
    }

    #home-apply-button {
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
    }

    #home-paintbrush{
      -webkit-transform: scale(1.5);
              transform: scale(1.5)
    }
  }
  @media (min-width:800px){

    #home-text-container{
      font-size: 3em;
      margin-top: 140px;
    }
  }
  @media (min-width:1100px){

    #home-text-container{
      margin: 0 auto;
      width: 40rem;
      font-size: 3.5em;
      margin-top: 200px;
    }

    #home-paintbrush {
      bottom:1rem;
      right: 0;
      -webkit-transform: translateX(70%) scale(2);
              transform: translateX(70%) scale(2);
    }

    .bigname {
      padding-top: 8rem;
    }
  }
  @media (min-width:1500px){
    main{
      overflow-y: auto;
      overflow: hidden;
    }
    #home-paintbrush {
      -webkit-transform: scale(3) rotateZ(15deg) translateX(30%);
              transform: scale(3) rotateZ(15deg) translateX(30%);
      bottom: -50px;
      right: 0px;
    }
   
  }

.about-container {
    height: 100vh;
    overflow-y: auto;
    overflow-x: hidden;
    padding-bottom: 10rem;
    width: 90%;
    margin: 0px auto;
}

.about-us {
    padding-top: 80px;
    color: var(--blue);
    /* width: 90%; */
    
}

section {
    margin: 20px 0;
}

section h1 {
    font-size: 2em;
    color: var(--orange);
    text-align: center;
    margin-bottom: 20px;
}

.about-us .rotate {
    background-color: rgb(216, 216, 216);
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: 70% 70%;
    padding: 20px;
    box-shadow: 1px 1px 4px 0px rgb(66, 66, 66);
    -webkit-transform: rotateZ(3deg);
            transform: rotateZ(3deg);
}

.about-us .rotate div {
    -webkit-transform: rotateZ(-3deg);
            transform: rotateZ(-3deg);
}



.curriculum div{
    color: var(--blue);
    background-color: rgb(216, 216, 216);
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    padding: 20px;
    box-shadow: 1px 1px 4px 0px rgb(66, 66, 66);
    background-position: 50% 50%;
}

.faculty div{
    color: var(--blue);
    background-color: rgb(216, 216, 216);
    padding: 20px;
    box-shadow: 1px 1px 4px rgb(66, 66, 66);
}

#about-icon-container{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    font-size: 2.5em;
}

section h4 {
    color: var(--blue);
    text-align: center;
    font-size: 1.5em;
    margin-bottom: 20px;
}

@media (min-width:600px){
    .rotate {
        width: 75%;
        margin: 40px auto;
    }

    .curriculum {
        width: 75%;
        margin: 80px auto;
    }

    .faculty {
        width: 75%;
        margin: 80px auto;
    }

    .about-container {
        font-size: 1.5em;
    }
   
}

@media (min-width:1100px){
    .rotate {
        width: 60%;
        margin: 0 auto;
    }

    .curriculum {
        width: 60%;
        margin: 40px auto;
    }

    .faculty {
        width: 60%;
        margin: 40px auto;
    }

    .about-container {
        font-size: 2em;
        padding-top: 6rem;
    }
}

@media (min-width:1800px){
    .rotate {
        width: 50%;
        margin: 0 auto;
    }

    .curriculum {
        width: 50%;
        margin: 40px auto;
    }

    .faculty {
        width: 50%;
        margin: 40px auto;
    }

    .about-container {
        font-size: 2em;
        padding-top: 6rem;
    }
    .about-container .rotate div{
       
        font-size: 1.2em;
        font-weight: bold;
        padding: 40px;
    }

    .faculty div{
        font-size: 1.2em;
        font-weight: bold;
        padding: 40px;
    }
    .curriculum div{
        font-size: 1.2em;
        font-weight: bold;
        padding: 40px;
    }
   
