.art-container {
    height: 100vh;
    padding-top: 6rem;
    width: 100%;
}

.art-container h1 {
    color: var(--orange);
    text-align: center;
    font-size: 2.2em;
}

.art-container h3 {
    color: var(--blue);
    text-align: center;
    font-size: 1em;
}

.art-container h2 {
   margin: 10px 0;
}

.art-container ol {
    color: var(--orange);
    text-align: center;
    margin: 40px auto;
    width: 80%;
    padding-bottom: 5rem;
}

.art-container p {
    text-align: left;
    color: var(--blue);
    margin: 10px auto;
    width: 80%;
    font-size: 1.1em;
    font-weight: bold;
}

.art-container li {
    margin: 60px 0;
}

.art-container ol li img {
    max-width: 80%;
    text-align: left;
    margin: 20px 0;
}

@media (min-width:600px){
    .art-container {
        font-size: 1.5em;
    }
    .art-container img {
        width: 100%;
        max-height: 100%;
    }
}

@media (min-width:1100px){
    .art-container {
        font-size: 2em;
        padding-top: 9rem;
    }
    
}

@media (min-width:1600px){
    .art-container {
        font-size: 2.5em;
        padding-top: 9rem;
    }
    
    .art-container p {
        font-size: 0.8em;
    }

    .art-container ol {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto;
    }
}
